@import url(https://fonts.googleapis.com/css2?family=Inter:wght@900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  box-sizing: border-box;
}

body {
  font-family: 'Helvetica', 'Arial', sans-serif;
  /* NEED TO CHECK FALLBACK FONT SINCE HELVETICA IS NOT SUPORTED ON WINDOWS */
  font-size: 18px;
  display: flex;
  margin: auto;
  padding: 20px;
  max-width: 700px;
  line-height: 1.5;

  
}

h1 {
  font-family: 'Inter', sans-serif;

}

a:link {
  background-color: rgb(235, 236, 237);
  color: #1565c0;
  margin-right: 10px;
}
a:visited {
  background-color: rgb(235, 236, 237);
  color: #1976d2;
}
a:hover{
background-color: #b7e4c7
}


.App {
  text-align: left;
  color: #394045
}

.divider {
  border-bottom: 1px solid rgba(235, 236, 237, 0.8);
  padding-bottom: 20px;
}

.profilephoto {
width: 12rem;
height: 12rem;
border-radius: 50%;
object-fit: cover;
}



.project-thumbnails{
  width: 500px;
  height: 300px;

}

/* .project-text{
  width: 500px;

} */

.projects-list{display:flex;flex-direction:column;justify-content:center;align-items:center}a{font-weight:bold}
.project-screen{border:2px solid #f3f2f2;border-radius:10px 10px 0px 0px;overflow:hidden;width:80%;margin:20px 10px;box-shadow:0 0 10px rgba(0,0,0,.15);height:300px}.p-browser{height:20px;background-color:#f3f2f2;display:flex;align-items:center;position:-webkit-sticky;position:sticky;z-index:2}.p-circle{width:6px;height:6px;border-radius:50%;margin:3px;background-color:#fff}.p-img{width:100%;transition:all 10s ease}.project-screen:hover .p-img{-webkit-transform:translateY(-100%);transform:translateY(-100%)}.project-legend h3{text-transform:uppercase}.project-legend a{margin-right:10px}
.header h1{margin:20px 0px 0px}.header p{margin:0px 0px 30px;font-size:16px}.header span{margin-right:5px}.header-block{display:flex}.header-links{margin-left:60px;font-size:16px}.header-links>h2{margin-top:10px;margin-bottom:15px}
.form-container{display:flex;flex-direction:column;align-items:center;justify-content:center;max-width:500px}form{display:flex;flex-direction:column;width:100%;background-color:#fff}input{display:flex;width:100%;height:50px;border:none;border-bottom:1px solid #000;margin:0px 0px;font-size:16px;padding-left:10px;padding-bottom:-10px}textarea{margin:10px 0px;font-size:16px;padding-left:10px;font-family:Arial,Helvetica,sans-serif}::-webkit-input-placeholder{text-transform:uppercase;padding:5px 0px}:-ms-input-placeholder{text-transform:uppercase;padding:5px 0px}::placeholder{text-transform:uppercase;padding:5px 0px}button{border:none;padding:15px;background-color:#59b256;color:#fff;font-weight:500;cursor:pointer;width:20%;height:10px;margin:10px 0px;display:flex;justify-content:center;align-items:center;border-radius:5px;text-transform:uppercase;font:13px Arial,sans-serif;font-weight:bold}
